.save-graph-editor-settings-button {
    width: 150px;
    max-height: 38px;
}

.save-div {
    -ms-flex-pack: end;
        justify-content: flex-end;
    display: -ms-flexbox;
    display: flex;
}

.app-bar-graph-editor-settings h2 {
    padding-left: 20px;
}

.slider-color>div>div{
    background: #448afe;
}

.slider-color>div>button{
    background: #448afe;
}

.colors-main-div {
    width: 200px;
    padding-top: 20px;
}

.colors-div {
    width: 48px;
    height: 48px;
}

.colors-div-checked {
    width: 48px;
    height: 48px;
    border: 1px solid white;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.colors-div-checked svg{
    font-size: 30px;
}

.graph-colors-setting-text-field {
    width: 100%;
}

p.graph-colors-setting-link {
    padding-top: 10px;
    font-size: 11px;
}

.graph-colors-setting-overflow-x{
    overflow-x: hidden;
}


