.Logo {
  position: relative;
  display: inline-block;
}

.nav-div {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.text-field-hierarchy-details label {
    padding-left: 30px;
}

div.text-field-hierarchy-details div {
    margin-top: 0;
}

.top-div-hierarchy-list {
    background-color: #e1e1db;
}

div .individuals-list {
    margin-left: 20px;
}

div .divider-individuals-style {
    margin-left: 20px;
    margin-right: 20px;
}

svg.details-label-icon {
    margin-right: 10px;
    margin-left: 10px;
    color: #4f43ae;
}

aside.nothing-selected {
    color: rgb(200,200,200);
    font-size: 20px;
    text-align: center;
    top: 45%;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    font-weight: 200;
}

div.top-div-hierarchy-list button {
    right: 0;
    background-color: #448afe;
    margin-top: 22px;
    margin-right: 20px;
    position: absolute;
}

div.top-div-hierarchy-list button:hover {
    background-color: #1976d2;
}

div.top-div-hierarchy-list button span {
    color: white;
}

div.top-div-hierarchy-list div.select-language-preference {
    margin-top: 0;
}

.data-type-property-color {
    color: #38A14A;
}

.object-property-color {
    color: #0079BA;
}

aside.restriction-nothing-display {
    color: rgb(200,200,200);
    font-size: 20px;
    text-align: center;
    font-weight: 200;
}

svg.individual-tree-icon {
    color: #874b82;
}


