.dsl-editor {
    height: 100%;
}

.dsl-editor button {
    right: 0;
    bottom: 0;
    background-color: #448afe;
    margin-bottom: 22px;
    margin-right: 20px;
    position: absolute;
    z-index: 999;
}

.dsl-editor button span {
    color: white;
}

.dsl-editor button:hover {
    background-color: #1976d2;
}


