
.tree-collapse {
    margin-left: 11px;
    border-left: 1px dotted black;
}

div.tree-list-item-dotted-alone:after {
  height: 2px;
  content: '';
  position: absolute;
  border-bottom: 1px dotted black;
  width: 63px;
  margin-left: -24px;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
 }

div.tree-list-item-dotted-collapse:after {
  height: 2px;
  content: '';
  position: absolute;
  border-bottom: 1px dotted black;
  width: 28px;
  margin-left: 10px;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
 }

div.tree-list-item-dotted:before {
    content: '';
    position: absolute;
    border-left: 1px dotted black;
    height: 100%;
    width: 100%;
    padding-bottom: 1px;
    -webkit-transform: translateX(11px);
        -ms-transform: translateX(11px);
            transform: translateX(11px);
}

svg.tree-icon {
    margin-right: 10px;
    margin-left: 10px;
    /*color: #663399;*/
