.Logo {
  position: relative;
  display: inline-block;
}

#container {
    width: 100%;
    height: 100%;
}

#left_panel {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 100px;
    background: grey;
}

#right_panel {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 50%;
    color: #fff;
    background: black;
}

#drag {
    position: absolute;
    left: -4px;
    top: 0;
    bottom: 0;
    width: 8px;
    cursor: w-resize;
}

.dialog-data-property-buttons {
    width: 100px;
}

svg.individual-data-property-icon {
    margin-right: 10px;
    margin-left: 10px;
    color: #38A14A;
}


