div.expanded-list-title {
   padding: 0 2px;
   letter-spacing: -.55px;
   height: 100%;
}

div.expanded-list-title span{
    line-height: 35px;
    color: #343434;
}

div.expanded-list-title-selected span{
    font-weight: 700;
}

div.expanded-list-title:first-child {
   padding-left: 35px;
}

nav.expanded-list {
    padding-top: 0px;
    padding-bottom: 0px;
    color: #343434;
}

nav.expanded-list > div[role="button"]{
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 0;
    padding-bottom: 0;
    height: 35px;
}

div.expanded-list-item {
    background: #f0f0f0;
}


