
header.app-bar-graph-editor-settings {
    position: relative;
}

.dialog-graph-editor-settings>div{
    background: #f5f5f5
}

.app-bar-graph-editor-settings h2 {
    padding-left: 30px;
}

.content-graph-editor-settings-div {
    height: inherit;
}

.content-graph-editor-settings-div>div{
    position: fixed;
    bottom: 0;
    width: 100%;
}

.graph-settings-tab {
    width: 70%;
}

.indicator-header span[style*="left: 0px; width: 160px;"] {
    background: #448afe;
}


