.svgArea{
    background-color:#1e1e1e;
    height: calc(100vh - (64px));
    position: relative;
    top: 64px;
}
.leftArea{
    z-index: 3;
    background-color:#333333;
    float: left;
    width: 50px;
    height: calc(100vh - (64px));
    position: absolute;
    left: 0;
    top: 64px;
    color: rgba(255,255,255, 0.6);
}

.leftArea button{
    color: rgba(255,255,255, 0.6);
}

.leftContentAreaAddElements{
    z-index: 2;
    background-color:#252526;
    width: 300px;
    height: 100vh;
    left: -300px;
    top: 64px;
    position: absolute;
}

.textEditor{
